.header{
    background-color: $main-header-color;
    position: fixed;
    top:0;
    left: 0;
    width: 100%;
    z-index: 99;
    height: $header-height;
    
    @include tablet {
        height: $header-tablet-height;
        
      
    }
    @include mobile {
        height: $header-mobile-height;
       
    }
   
   .container{
       height: 100%;
   }
    &__logo{
        @include flex(center,center);
        position: absolute;
        inset: 0;
        height:$header-height;
        pointer-events: none;
        @include tablet{
            @include flex(flex-start,flex-start);
             top: -10px;
             margin-left:100px;
            
        }
        @include mobile{
            @include flex(center,center);
            margin-left:0;
            top: -$header-mobile-height;
        }
        img{
            height: 250px;
            @include tablet{
                height: 100px;
              
            }
            @include mobile{
                height:70px;
            }
        }
    }

    &__menu{
        @include flex(center, space-between);
        height: 100%;
        font-size: 1.5rem;
       
        &__mobile-toggle{
            display: none;
            top: 0;
        }
       
        &__item ~ &__item{
            margin-left: 30px;
         
            @include tablet{
                margin-left:10px;
            }
        }
        &__item{
           
            .active{
                font-weight: 500;
                color:$main-color;
        }
       
      
        }
        &__item:hover{
            color: $main-color;
            cursor: pointer;
        }
        &__left, &__right{
            @include flex(center,center);
            height: 100%;
           
        }
        &__right{
            font-size: 1.5rem;
            @include tablet{
                font-size: 1.2rem;
                
            }
            @include mobile{
                @include flex(flex-end,flex-start); 
                    flex-direction: column;
                    background-color: $main-bg;
                    position: absolute;
                    right: 0;    
                    top: 0;
                    padding: 30px;
                   width: 50%;
                    padding-top: 20px;
                    height: 100vh;
                    text-align: center;
                    transform: translateX(100%);
                    transition: transform .5s ease;
                    &.active{
                        transform: translateX(0);
                    }
                    &__close{
                        display: block;
                        position: absolute;
                        left: 20px;
    
                    }
                    &__item ~ &__item{
                        margin-left: unset;
                        margin-top: 20px;
                        background-color: $main-bg;
                      
                    }
               
            }
            &__item{
                @include mobile{
                    &:first-child{
                        display: none;
                    }
                }
              
                input{
                    outline: none;
                    padding: 4px;               
                    border-radius: 10px;
                    font-size: 1rem;
                    width: 100px;
                    margin-right: 5px;
                    margin-top: auto;
                    border: solid 2px;
                    &:hover{
                        border-color:$main-color;
                    }
                    @include tablet{
                        font-size:0.8rem;
                    }
                    &::placeholder{
                        font-size: 1.2rem;
                        @include tablet{
                            font-size: 1rem;
                        }
                    }
                    &:focus{
                        
                        
                     width: 200px;               
                      border-color:$main-color ; 
                      
                    
                        &::placeholder{
                            color: #fff; 
                           
                        }
                    }
                   
                }
                @include flex(center,center);
               
            }
            @include tablet{
                margin-left: auto;
            }
            @include mobile{
                font-size: 1.5rem;
            }
            .active{
                font-weight: 500;
                color:$main-color;
            }
        }
        &__left{
            font-size: 1.5rem;
            &__close{
                display: none;
                font-size: 2.5rem;
            }
            @include tablet{     
              
                flex-direction: column;
                background-color: $main-bg;
                position: absolute;
                left: 0;    
                top: 0;
                padding-top: 20px;
                width: 100%;
                height: 100vh;
                justify-content: flex-start;
                transform: translateX(-100%);
                transition: transform .5s ease;
                &.active{
                    transform: translateX(0);
                }
                &__close{
                    display: block;
                    position: absolute;
                    left: 20px;
                    top:5px;

                }
                &__item ~ &__item{
                    margin-left: unset;
                    margin-top: 20px;
                    background-color: $main-bg;
                  
                }
           }
           @include mobile {
            @include flex(flex-start,flex-start); 
            flex-direction: column;
            background-color: $main-bg;
            position: absolute;
            left: 0;    
            top: 0;
            font-size: 1.5rem;
            padding: 30px;
            padding-top: 40px;
            width: 50%;
            height: 100vh;
           
            transform: translateX(-100%);
            transition: transform .5s ease;
            &.active{
                transform: translateX(0);
            }
            &__close{
                display: block;
                position: absolute;
                left: 20px;

            }
            &__item ~ &__item{
                margin-left: unset;
                margin-top: 20px;
                background-color: $main-bg;
              
            }
           }
            .active{
                font-weight: 500;
                color:$main-color;
            }
          
           
           
        }
        &__mobile-toggle{
            display: none;
            @include tablet{
                display:block;
                font-size: 2.5rem;
            }
        }
    }
}